<template>
  <div>
    <div class="fasilitas-daftar-jenis">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header class="pt-4 pb-8" type="primary" />
      <div class="container-fluid mt--8">
        <div class="card">
          <div class="card-header">
            <div class="row">
              <div class="col-6">
                <div class="row align-items-center h-100">
                  <div class="col">
                    <h3 class="mb-0">Daftar Jenis Fasilitas</h3>
                  </div>
                </div>
              </div>
              <div class="col-6 text-right">
                <base-button
                  class="border-0"
                  size="md"
                  type="primary"
                  @click="openNewForm"
                  >Tambah</base-button
                >
              </div>
            </div>
          </div>

          <el-table
            class="table-responsive table-flush"
            header-row-class-name="thead-light"
            :data="queriedData"
            :default-sort="{ prop: 'timestamp', order: 'descending' }"
            @sort-change="sortChange"
            @selection-change="selectionChange"
            highlight-current-row
          >
            <el-table-column
              min-width="360px"
              label="Jenis Fasilitas"
              prop="label"
            ></el-table-column>
            <el-table-column min-width="72px" align="right">
              <div
                slot-scope="{ $index, row }"
                class="d-flex justify-content-end"
              >
                <base-button
                  @click.native="handleEdit($index, row)"
                  round
                  class="btn-icon-only"
                  type="info"
                  size="sm"
                >
                  <span class="btn-inner--icon"
                    ><i class="ni ni-settings-gear-65"></i
                  ></span>
                </base-button>
                <base-button
                  @click.native="handleDelete($index, row)"
                  round
                  class="btn-icon-only"
                  type="warning"
                  size="sm"
                >
                  <span class="btn-inner--icon"
                    ><i class="ni ni-fat-remove"></i
                  ></span>
                </base-button>
              </div>
            </el-table-column>
          </el-table>
          <div
            slot="footer"
            class="col-12 px-4 py-2 d-flex justify-content-center justify-content-sm-between flex-wrap"
          >
            <div class="d-flex align-items-center">
              <p class="card-category mb-0 mr-2">Menampilkan</p>
              <div style="width: 80px">
                <el-select
                  class="select-primary pagination-select"
                  v-model="pagination.perPage"
                  placeholder="Per page"
                  size="mini"
                >
                  <el-option
                    class="select-primary"
                    v-for="item in pagination.perPageOptions"
                    :key="item"
                    :label="item"
                    :value="item"
                  ></el-option>
                </el-select>
              </div>
              <p class="card-category m-0 ml-2">
                Tampil {{ from + 1 }} sampai {{ to }} dari {{ total }} data
                <span v-if="selectedRows.length"
                  >&nbsp; &nbsp; {{ selectedRows.length }} rows selected</span
                >
              </p>
            </div>
            <base-pagination
              class="pagination-no-border my-3"
              v-model="pagination.currentPage"
              :per-page="pagination.perPage"
              :total="total"
            ></base-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination } from "@/components";
import moment from "moment";
import clientPaginationMixin from "../../Tables/PaginatedTables/clientPaginationMixinMista";
import cabang from "../cabang";
import detail from "./detail";
import Axios from "axios";
import Swal from "sweetalert2";

const intRNG = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
};

export default {
  mixins: [clientPaginationMixin],
  components: {
    BasePagination,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  data() {
    return {
      cabang,
      detail,
      selects: {
        unit: 9999,
      },
      tableData: [],
      selectedRows: [],
    };
  },
  computed: {},
  methods: {
    getData(query) {
      let dummy = {
        word: [
          "Lorem",
          "Ipsum",
          "Dolor",
          "Sit",
          "Amet",
          "Consectetur",
          "Adipiscing",
          "Elit",
          "Sed",
          "Do",
          "Eismod",
          "Tempor",
          "Incididunt",
          "Ut",
          "Labore",
          "Et",
          "Dolore",
          "Magna",
          "Aliqua",
        ],
        sentence: [
          "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
          "Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",
          "Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.",
          "Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
        ],
      };
      switch (query) {
        case "dummy":
          const count = 1000;
          for (let i = 0; i < count; i++) {
            this.tableData.push({
              value: i,
              label:
                dummy.word[intRNG(0, dummy.word.length - 1)] +
                " " +
                dummy.word[intRNG(0, dummy.word.length - 1)],
            });
          }
          break;

        default:
          Axios.get(
            this.$store.getters.getAPI.url +
              this.$store.getters.getAPI.fs.getType
          )
            .then((response) => {
              if (this.$store.getters.isDebugging) {
                console.log(
                  "DEBUG: Success to get rating list. Data: ",
                  response.data
                );
              }
              let array = [];
              for (let i = 0; i < response.data.length; i++) {
                array.push({
                  value: response.data[i].ID,
                  label: response.data[i].NAME,
                });
              }
              array.sort((a, b) => {
                let aVal = a.label;
                let bVal = b.label;
                return bVal - aVal;
              });
              this.tableData = array;
              this.$store.commit("setJenisFasilitas", array);
            })
            .catch((error) => {
              if (this.$store.getters.isDebugging) {
                console.log("ERROR: Failed to get rating list. ", error);
                this.$router.push("form/tambah");
              }
            });
          break;
      }
    },
    openNewForm() {
      this.$router.push("form/tambah");
    },
    handleEdit(index, row) {
      this.$store.commit("setUbahJenisFasilitas", row);
      this.$router.push("form/ubah");
    },
    handleDelete(index, row) {
      // console.log(index, row);
      Swal.fire({
        icon: "question",
        title: "Hapus Data",
        text: "Lanjutkan untuk menghapus jenis " + row.label + "?",
        showCancelButton: true,
        confirmButtonText: `Hapus`,
        cancelButtonText: `Batal`,
      }).then((result) => {
        if (result.isConfirmed) {
          // console.log(
          //   this.$store.getters.getAPI.url +
          //     this.$store.getters.getAPI.fs.deleteType +
          //     row.value
          // );
          Axios.delete(
            this.$store.getters.getAPI.url +
              this.$store.getters.getAPI.fs.deleteType +
              row.value
          )
            .then((response) => {
              Swal.fire({
                icon: "success",
                title: "Berhasil menghapus data",
              });
              this.getData();
            })
            .catch((error) => {
              Swal.fire({
                icon: "error",
                title: "Gagal menghapus data",
              });
            });
        }
      });
    },
    openItemMenu(row, col, event) {
      console.log(row);
      Swal.fire({
        title: "Do you want to save the changes?",
        showDenyButton: true,
        showCancelButton: true,
        confirmButtonText: `Save`,
        denyButtonText: `Don't save`,
      });
    },
    getUnitName(id) {
      for (let index = 0; index < this.cabang.length; index++) {
        if (this.cabang[index].value === id) {
          return this.cabang[index].label;
        }
      }
    },
    unixTS2DMY(timestamp) {
      return moment.unix(timestamp).format("DD/MM/YYYY");
    },
    selectionChange(selectedRows) {
      this.selectedRows = selectedRows;
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style lang="scss">
.fasilitas-daftar-jenis {
  .el-table {
    .el-table__row {
      &:hover {
        background: transparent;
        cursor: unset;
      }
    }
  }
}
</style>